@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200&family=Poppins:wght@300;400;500;600&display=swap');

*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;

   
}

html{
    scroll-behavior:smooth;

    scrollbar-width: thin;
}

.parent-item{
    display: grid;
   grid-template-columns: repeat(3, 1fr);
   margin-left: 120px;
}